import { Avatar } from '@material-ui/core';
import React, {useState} from 'react';
import './MessageSender.css';
import {MdVideocam, MdPhotoLibrary,MdInsertEmoticon} from 'react-icons/md';
import { useStateValue } from './StateProvider';
import db from "./firebase";
import firebase from "firebase";
function MessageSender() {
    const [{ user }, dispatch] = useStateValue();
    const [input, setInput] = useState('');
    const [imageUrl, SetImageUrl] = useState("");


    const handleSubmit = e =>{
        e.preventDefault();
        
        db.collection('posts').add({
            message: input, 
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            profilePic: user.photoURL,
            username: user.displayName,
            image: imageUrl
        });


        setInput("");
        SetImageUrl("");
    };
    return (

        
        <div className="messageSender">
            <div className="messageSender__top">
                <Avatar src={user.photoURL}/>
                <form>
                    <input
                     value={input}
                     onChange={(e) => setInput(e.target.value)}
                     className="messagesender__input" placeholder={`What's on your mind, ${user.displayName} ?`} />
                    <input
                      value={imageUrl}
                      onChange={(e) => SetImageUrl(e.target.value)}
                      placeholder={"Image URL (optional) "}/>
                    
                    <button onClick={handleSubmit} type="submit">
                        Hidden submit
                    </button>

                </form>
                
            </div>

            <div className="messageSender__bottom">
                <div className="messageSender__option">
                    <MdVideocam style={{color:"red"}} />
                    <h3>Live video</h3>
                </div> 

                <div className="messageSender__option">
                    <MdPhotoLibrary style={{color:"green"}} />
                    <h3>Photo/Video</h3>
                </div> 

                <div className="messageSender__option">
                    <MdInsertEmoticon style={{color:"orange"}} />
                    <h3>Feeling/Activity</h3>
                </div> 
                
            </div>
            
        </div>
    )
}

export default MessageSender
